@charset "UTF-8";
.ol-popup {
  position: absolute;
  background-color: white;
  -webkit-filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
  filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
  padding: 15px 10px;
  border: 1px solid #cccccc;
  bottom: 20px;
  left: -50px;
  min-width: 230px;
  max-width: 230px; }

.ol-popup:after, .ol-popup:before {
  top: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none; }

.ol-popup:after {
  border-top-color: white;
  border-width: 10px;
  left: 48px;
  margin-left: -10px; }

.ol-popup:before {
  border-top-color: #cccccc;
  border-width: 11px;
  left: 48px;
  margin-left: -11px; }

.ol-popup-closer {
  text-decoration: none;
  position: absolute;
  top: 12px;
  right: 15px; }

.ol-popup-closer:after {
  content: "✖";
  color: #000; }

.popup__header {
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 5px;
  text-transform: uppercase;
  border-bottom: 1px solid #D2D3D8; }

.popup__table td, .popup__table tr {
  font-size: 12px;
  line-height: 21px;
  color: #34394C; }
  .popup__table td .MuiSvgIcon-root, .popup__table tr .MuiSvgIcon-root {
    font-size: 1rem;
    margin-left: 2px; }
